/* Reset et variables */
:root {
    --spacing: clamp(1rem, 2vw, 2rem);
    --content-width: min(90vw, 1200px);
    --primary-bg: #f8f9fa;
    --text-dark: #2c3e50;
    --text-light: #7f8c8d;
    --accent: #667eea;
}

/* Layout de base */
.parcours-page {
    min-height: 100vh;
    width: 100vw;
    overflow-x: hidden;
    background: white;
}

.parcours-container {
    width: var(--content-width);
    margin: 0 auto;
    padding: var(--spacing);
}

/* Layout pour le défilement horizontal */
.horizontal-scroll-container {
    display: flex;
    overflow-x: auto;
    overflow-y: hidden;
    scroll-snap-type: x proximity;
    width: 100vw;
    height: 100vh;
    scroll-behavior: smooth;
    -webkit-overflow-scrolling: touch;
}

.scroll-slide {
    flex: 0 0 100%;
    width: 100vw;
    height: 100vh;
    scroll-snap-align: center;
    scroll-snap-stop: always;
    padding: 2rem 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: visible;
    position: relative;
}

/* Contenu du slide */
.parcours-section {
    display: flex;
    gap: 1.5rem;
    height: 100%;
    padding: 0 5%;
    align-items: center;
    /* Ensure section doesn't overflow vertically if content is huge, though text should scroll */
    max-height: 90vh;
}

.parcours-section-text {
    flex: 0 0 60%;
    overflow-y: visible;
    padding: 0.8rem;
    padding-bottom: 1rem;
}

/* Scrollbar webkit support */
.parcours-section-text::-webkit-scrollbar {
    width: 6px;
}

.parcours-section-text::-webkit-scrollbar-track {
    background: #f1f1f1;
}

.parcours-section-text::-webkit-scrollbar-thumb {
    background: #ccc;
    border-radius: 3px;
}

.parcours-images {
    flex: 0 0 35%;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
}

/* Styles de texte */
.parcours-text {
    color: var(--text-dark);
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-family: 'Courier New', Courier, monospace;
    /* Police monospace comme la page principale */
}

.parcours-text h2 {
    font-size: clamp(1.5rem, 2.5vw, 2rem);
    margin-bottom: 0.8rem;
    color: var(--text-dark);
}

.parcours-text h3 {
    font-size: clamp(1.1rem, 2vw, 1.5rem);
    margin-bottom: 0.4rem;
    font-weight: 700;
    color: var(--text-dark);
    letter-spacing: 0.5px;
    text-transform: uppercase;
}

.parcours-text p,
.parcours-text li {
    font-size: clamp(1.1rem, 1.2vw, 1.2rem);
    line-height: 1.4;
    margin-bottom: 0.5rem;
    color: var(--text-light);
    text-align: justify;
}

.skills-list {
    list-style: none;
    padding: 0;
    margin: 0.5rem 0;
}

.skills-list li {
    padding: 0.3rem 0;
    margin: 0;
    font-size: clamp(0.9rem, 1.2vw, 1rem);
    line-height: 1.4;
    color: var(--text-light);
}

.skills-sublist {
    list-style: none;
    padding: 0.3rem 0 0.3rem 1.5rem;
    margin: 0.2rem 0 0 0;
}

.skills-sublist li {
    padding: 0.2rem 0;
    margin: 0;
    font-size: clamp(0.85rem, 1.1vw, 0.95rem);
    line-height: 1.3;
    color: var(--text-light);
}

/* Images - Design moderne */
.parcours-image {
    width: clamp(250px, 80%, 400px);
    height: auto;
    object-fit: contain;
    border-radius: 0;
    box-shadow: none;
}

.list-stage-img {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    width: 100%;
    padding: 1rem;
}

.list-stage-img img {
    width: 80px;
    height: 80px;
    object-fit: contain;
    border-radius: 0;
    box-shadow: none;
}

.parcours-section-add img {
    width: 180px;
    height: auto;
    object-fit: contain;
    border-radius: 0;
    box-shadow: none;
}

/* Ajuster le conteneur d'images */
.parcours-images {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1.2rem;
    margin: 0 auto;
}

/* Images - Styles améliorés */
.parcours-images {
    flex: 0 0 35%;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
}

.parcours-image {
    width: clamp(250px, 80%, 400px);
    height: auto;
    object-fit: contain;
    border-radius: 0;
    box-shadow: none;
}

.parcours-image:hover {
    transform: scale(1.05);
}

/* Images des stages */
.list-stage-img {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.8rem;
    width: 100%;
    padding: 1rem;
}

.list-stage-img img {
    width: 100px;
    height: 100px;
    object-fit: contain;
    border-radius: 0;
    box-shadow: none;
}

/* Images supplémentaires */
.parcours-section-add img {
    width: 140px;
    height: auto;
    object-fit: contain;
}

/* Ajustement pour 3 images */
.list-stage-img:has(> img:nth-child(3)) img {
    width: 70px;
    height: 70px;
}

.parcours-section-add:has(> img:nth-child(3)) img {
    width: 120px;
}

.parcours-images:has(> .parcours-image:nth-child(3)) .parcours-image {
    width: calc(clamp(250px, 80%, 400px) - 30px);
}

/* Hide scroll-right hint in column mode */
@media (max-width: 1300px) {
    .scroll-right {
        display: none;
    }

    .horizontal-scroll-container {
        flex-direction: column;
        overflow-x: hidden;
        overflow-y: auto;
        height: auto;
        scroll-snap-type: none;
    }

    .scroll-slide {
        flex: none;
        min-height: auto;
        height: auto;
        width: 100%;
        scroll-snap-align: none;
        overflow-y: visible;
        padding-top: 80px;
    }

    .parcours-section {
        flex-direction: column;
        height: auto;
        overflow: visible;
        max-height: none;
    }

    .parcours-section-text,
    .parcours-images {
        width: 100%;
        overflow: visible;
        flex: none;
    }

    .parcours-section-text {
        max-height: none;
        overflow-y: visible;
    }

    .parcours-text h3 {
        font-size: clamp(0.95rem, 1.5vw, 1.2rem);
        margin-bottom: 0.3rem;
    }

    .parcours-text p,
    .parcours-text li {
        font-size: clamp(0.8rem, 1vw, 0.95rem);
        line-height: 1.3;
        margin-bottom: 0.3rem;
    }

    .parcours-images {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        padding: 1rem;
        margin-top: 1rem;
    }

    /* Ajuster les tailles des images pour mobile */
    .parcours-image {
        width: 80%;
        max-width: 250px;
    }

    .list-stage-img {
        flex-wrap: wrap;
    }
}

@media (max-width: 768px) {

    /* ...existing code... */
    .parcours-section-text h3 {
        font-size: 1.3rem;
        /* Slightly smaller */
    }

    .parcours-image {
        width: 60%;
        max-width: 200px;
    }

    .list-stage-img img {
        width: 80px;
        height: 80px;
    }

    .parcours-section-add img {
        width: 120px;
        height: auto;
    }

    /* Ajustement pour 3 images en mobile */
    .list-stage-img:has(> img:nth-child(3)) img {
        width: 70px;
        height: 70px;
    }

    .parcours-section-add:has(> img:nth-child(3)) img {
        width: 100px;
    }
}

@media (max-width: 400px) {
    .parcours-image {
        width: 40%;
        max-width: 150px;
    }

    .list-stage-img img {
        width: 60px;
        height: 60px;
    }

    .parcours-section-add img {
        width: 80px;
    }

    /* Ajustement pour 3 images en très petit mobile */
    .list-stage-img:has(> img:nth-child(3)) img {
        width: 45px;
        height: 45px;
    }

    .parcours-section-add:has(> img:nth-child(3)) img {
        width: 70px;
    }

    /* Ajuster l'espacement */
    .parcours-images {
        gap: 1rem;
    }

    .list-stage-img {
        gap: 0.5rem;
        padding: 0.5rem;
    }

    /* Réduire les margins à gauche sur petit écran */
    .parcours-section {
        padding: 0 1rem;
    }

    .parcours-text h2 {
        font-size: 1.5rem;
    }

    .parcours-text h3 {
        font-size: 1.2rem;
    }

    .parcours-text p,
    .parcours-text li {
        font-size: 1rem;
    }
}

/* Style de la scrollbar horizontale */
.horizontal-scroll-container {
    scrollbar-width: thin;
    scrollbar-color: #888 #f1f1f1;
}

.horizontal-scroll-container::-webkit-scrollbar {
    height: 8px;
}

.horizontal-scroll-container::-webkit-scrollbar-track {
    background: #f1f1f1;
}

.horizontal-scroll-container::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 4px;
}

/* Styles de la scrollbar */
.cv-content {
    /* Style de la scrollbar */
    scrollbar-width: thin;
    scrollbar-color: #888 #f1f1f1;
}

.cv-content::-webkit-scrollbar {
    width: 6px;
}

.cv-content::-webkit-scrollbar-track {
    background: #f1f1f1;
}

.cv-content::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 3px;
}

/* Ajuster la taille du texte pour mieux s'adapter */
@media (max-height: 800px) {
    .cv-content {
        font-size: 0.9em;
    }
}

@media (max-height: 600px) {
    .cv-content {
        font-size: 0.8em;
    }
}

.skills-list {
    list-style: none;
    padding: 0;
    margin: 0;
}